"use strict";
var Data_Maybe = require("../Data.Maybe");
var Halogen_VDom = require("../Halogen.VDom");
var Halogen_VDom_Types = require("../Halogen.VDom.Types");
var Prelude = require("../Prelude");
var UI_Core = require("../UI.Core");
var element = function (elemName) {
    return function (props) {
        return function (children) {
            return new Halogen_VDom_Types.Elem(new Halogen_VDom_Types.ElemSpec(Data_Maybe.Nothing.value, elemName, props), children);
        };
    };
};
var leaf = function (elem) {
    return function (props) {
        return element(elem)(props)([  ]);
    };
};
var imageView = leaf("imageView");
var listView = leaf("listView");
var progressBar = leaf("progressBar");
var $$switch = leaf("switch");
var textView = leaf("textView");
var viewWidget = leaf("viewWidget");
var webView = leaf("webView");
var node = function (elem) {
    return element(elem);
};
var frameLayout = node("frameLayout");
var horizontalScrollView = node("horizontalScrollView");
var linearLayout = node("linearLayout");
var relativeLayout = node("relativeLayout");
var scrollView = node("scrollView");
var shimmerFrameLayout = node("shimmerFrameLayout");
var tabLayout = node("tabLayout");
var viewPager = node("viewPager");
var editText = leaf("editText");
var checkBox = leaf("checkBox");
var calendar = leaf("calendar");
var button = leaf("button");
module.exports = {
    element: element,
    linearLayout: linearLayout,
    relativeLayout: relativeLayout,
    textView: textView,
    editText: editText,
    scrollView: scrollView,
    imageView: imageView,
    button: button,
    listView: listView,
    checkBox: checkBox,
    horizontalScrollView: horizontalScrollView,
    progressBar: progressBar,
    viewPager: viewPager,
    calendar: calendar,
    frameLayout: frameLayout,
    shimmerFrameLayout: shimmerFrameLayout,
    "switch": $$switch,
    tabLayout: tabLayout,
    viewWidget: viewWidget,
    webView: webView
};



