"use strict";
var Data_Tuple = require("../Data.Tuple");
var Prelude = require("../Prelude");
var UI_Core = require("../UI.Core");
var onMouseDown = function (some) {
    return new Data_Tuple.Tuple("onMouseDown", some);
};
var onClick = function (some) {
    return new Data_Tuple.Tuple("onClick", some);
};
var onChange = function (some) {
    return new Data_Tuple.Tuple("onChange", some);
};
var domName = function (st) {
    return new Data_Tuple.Tuple("domName", st);
};
module.exports = {
    domName: domName,
    onClick: onClick,
    onChange: onChange,
    onMouseDown: onMouseDown
};



